.body {
	position: fixed;
	top:100px;
	width: 100%;
	left: 0;
	height: calc(100% - 200px);
}
.bouton {
	position: absolute;
	top: 0;
	left: 15%;
	width: 70%;
	height: 100%;
	background: url(/static/img/buzzez.3178a16.png) no-repeat center center;
	background-size:contain;
	-webkit-transition:opacity 0.5s ease, -webkit-transform 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335);
	transition:opacity 0.5s ease, -webkit-transform 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335);
	transition:transform 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 0.5s ease;
	transition:transform 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 0.5s ease, -webkit-transform 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335); 
	-webkit-transform: scale(0.1); 
	        transform: scale(0.1);
	opacity: 0;
	z-index: 100;
}
.animationBouton .bouton{
	-webkit-transform: scale(1);
	        transform: scale(1);
	opacity: 1;
}

.lignes {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.lignes-gauche {
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
}
.lignes-droite {
	position: absolute;
	top: 0;
	right: 0;
	width: 50%;
	height: 100%;
}
.ligne {
	position: absolute;
	top: 0;
	height: 100%;
}
.lignes-gauche .ligne{
	left: -50%;
	-webkit-transition: 0.5s ease;
	transition: 0.5s ease;
}
.lignes-droite .ligne{
	right: -50%;
	-webkit-transition: 0.5s ease;
	transition: 0.5s ease;
}
.lignes.ok .lignes-gauche .ligne{
	left: 0;
}
.lignes.ok .lignes-droite .ligne{
	right: 0;
}
.lignes-gauche .ligne:nth-child(1){
	width:8%;
	background: #231f20;
	z-index: 4;
	-webkit-transition: 0.2s ease;
	transition: 0.2s ease;
}
.lignes-gauche .ligne:nth-child(2){
	width:12%;
	background: #ed1c24;
	z-index: 3;
	-webkit-transition: 0.5s ease;
	transition: 0.5s ease;
}
.lignes-gauche .ligne:nth-child(3){
	width:15%;
	background: #cededd;
	z-index: 2;
	-webkit-transition: 0.7s ease;
	transition: 0.7s ease;
}
.lignes-gauche .ligne:nth-child(4){
	width:25%;
	background: #c0924b;
	z-index: 1;
	-webkit-transition: 1s ease;
	transition: 1s ease;
}
.lignes-droite .ligne:nth-child(1){
	width:9%;
	background: #7b8990;
	z-index: 4;
	-webkit-transition: 0.1s ease;
	transition: 0.1s ease;
}
.lignes-droite .ligne:nth-child(2){
	width:12%;
	background: #231f20;
	z-index: 3;
	-webkit-transition: 0.5s ease;
	transition: 0.5s ease;
}
.lignes-droite .ligne:nth-child(3){
	width:16%;
	background: #c0924b;
	z-index: 2;
	-webkit-transition: 0.7s ease;
	transition: 0.7s ease;
}
.lignes-droite .ligne:nth-child(4){
	width:19%;
	background: #ed1c24;
	z-index: 1;
	-webkit-transition: 1s ease;
	transition: 1s ease;
}


.compteur {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-transform: scale(0);
	        transform: scale(0);
	opacity: 0;
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
}
.boutonReady {
	-webkit-transform: scale(1);
	        transform: scale(1);
	opacity: 1;
}
.chiffre {
	position: absolute;
	top: 25%;
	left: 0;
	width: 100%;
	height: 50%;
	text-align: center;
	font-size: 23rem;
	color: white;
	text-shadow: 6px 6px black;
	font-weight: bold;
	z-index: 200;
	-webkit-animation-name: warp;
	        animation-name: warp;
	-webkit-animation-duration: 500ms;
	        animation-duration: 500ms;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;
}
.star:after {
	display: block;
	content:'';
	position: absolute;
    top: calc(50% - 300px);
    left: calc(50% - 300px);
    width: 600px;
    height: 600px;
    background: url(/static/img/star.b880941.png) no-repeat center center;
	background-size: contain;
	z-index: -1;
	-webkit-animation-name: spin;
	        animation-name: spin;
	-webkit-animation-duration: 1000ms;
	        animation-duration: 1000ms;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;	
	-webkit-transform-origin: 50% 52.5%;	
	        transform-origin: 50% 52.5%;
}
@-webkit-keyframes spin {
from {-webkit-transform:rotate(0deg) scale(0.5);transform:rotate(0deg) scale(0.5);
}
to {-webkit-transform:rotate(360deg) scale(1);transform:rotate(360deg) scale(1);
}
}
@keyframes spin {
from {-webkit-transform:rotate(0deg) scale(0.5);transform:rotate(0deg) scale(0.5);
}
to {-webkit-transform:rotate(360deg) scale(1);transform:rotate(360deg) scale(1);
}
}
@-webkit-keyframes warp {
0% {-webkit-transform:scale(0.9);transform:scale(0.9);
}
50% {-webkit-transform:scale(1);transform:scale(1);
}
100% {-webkit-transform:scale(0.9);transform:scale(0.9);
}
}
@keyframes warp {
0% {-webkit-transform:scale(0.9);transform:scale(0.9);
}
50% {-webkit-transform:scale(1);transform:scale(1);
}
100% {-webkit-transform:scale(0.9);transform:scale(0.9);
}
}
